import {
	msgtemplatePages,
	//	msgtemplateMsg,
	msgtemplateAdd,
	msgtemplateUpdate,
	msgtemplateDelete,
	//	msgtemplateDetail
} from 'api/api';
import storyform from './msgTemplateForm/msgTemplateForm';
let pageData = {
	createTime:"",
	creator:"",
	language:"",
	modifyTime:"",
	modifyor:"",
	tempTitle:"",
	tempContent:"",
	tempEnUsContent:"",
	tempEnUsTitle:"",
	tempJaJpContent:"",
	tempJaJpTitle:"",
	tempKoKrContent:"",
	tempKoKrTitle:"",
	tempZhCnContent:"",
	tempZhCnTitle:"",
	templateCode:"",
	templateId:"",
	tempVars:"",
	noticeType:"",
	sendType:"",
	templateStatus:"",
}
export default {
	data() {
		return {
			searchValue: '',
			tableData: {},
			dialogVisible: false,
			dialogTyle: '',

			page: {
				start: 0,
				length: 10,
				draw: 1,
			},
			currentPage: 1,
			pageSizes: [10, 20, 30],

			pageData: {
				...pageData
			},
		}
	},

	components: {
		storyform,
	},
	created() {
		this.init(this.page)
	},
	watch: {
		searchValue(value) {
			if(!value) {
				msgtemplatePages(this.page).then(res => {
					this.tableData = res.data.obj
				})
			}
		}
	},
	methods: {
		init(obj) {
			msgtemplatePages(this.page).then(res => {
				this.tableData = res.data.obj
			})
		},
		onQuery() {
			if(this.searchValue) {
				msgtemplatePages({ ...this.page,
					tempTitle: this.searchValue
				}).then(res => {
					this.tableData = res.data.obj
				})
			}
		},
		showDilog(type, obj = pageData) {
			this.dialogVisible = true
			this.dialogTyle = type
			this.pageData = { ...obj
			}

		},
		onSubmit() {
			this.$refs.storyForm.validate(valid => valid && this.commitForm())
		},
		commitForm() {
			for(let key in this.pageData) {
				if(key.indexOf('Time') > -1) {
					delete this.pageData[key]
				}
			}
			if(this.dialogTyle === 'edit') {

				msgtemplateUpdate(this.pageData).then(res => {
					this.init(this.page)
					this.dialogVisible = false
				})
			} else if(this.dialogTyle === 'add') {
				msgtemplateAdd(this.pageData).then(res => {
					this.init(this.page)
					this.dialogVisible = false
				})
			}
		},
		handleDelete(obj) {
			this.$confirm(this.$t('commom.deleConfim'), '', {
				confirmButtonText: this.$t('commom.sure'),
				cancelButtonText: this.$t('commom.cancel'),
				type: 'warning'
			}).then(() => {

				msgtemplateDelete({
					templateId: obj.templateId
				}).then(res => {
					this.init(this.page)
				})

			}).catch(res => {

			})

		},
		getpageData() {
			msgtemplatePages({ ...this.page,
				ruleTitle: this.searchValue
			}).then(res => {
				this.tableData = res.data.obj
			})
		},
		handleSizeChange(val) {
			this.page.length = val
			this.page.start = (this.currentPage - 1) * this.page.length
			this.getpageData()
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			this.page.start = (this.currentPage - 1) * this.page.length
			setTimeout(() => {
				this.getpageData()
			}, 0)
		},
	}
}



